import { Component } from "@angular/core";
import { UserInfo } from "../classes/userInfo";
import { Subscription } from 'rxjs';
import { InMemoryDataService } from '../in-memory-data.service';
import { Credentials } from '../classes/credentials';


@Component({
  selector: "app-footer",
  templateUrl: "./footer.component.html",
  styleUrls: ["./footer.scss"]
})
export class FooterComponent {
  

  userInfo: UserInfo = new UserInfo();
  username: string = "";
  userSubscription: Subscription;
  usernameSubscription: Subscription;
  dataService: InMemoryDataService;
  testSubscription: Subscription;

  constructor(dataService: InMemoryDataService) {
    this.dataService = dataService;
    this.userInfo.isAdmin = false;
  }

  public get currentYear(): number {
    return (new Date()).getFullYear();
  }
  ngOnInit() {
    this.userSubscription = this.dataService
      .getUserInfo()
      .subscribe((userInfo: UserInfo) => {
        this.userInfo = userInfo;
      });
    this.usernameSubscription = this.dataService
      .getUsername()
      .subscribe((credentials: string) => {
        this.username = credentials;
      });

  }
}
