import { Component, OnInit } from "@angular/core";
import { EditEpisodeService } from "../edit-episode.service";
import { Subscription } from "rxjs";
import { Episode } from "../classes/episode";
import { InMemoryDataService } from "../in-memory-data.service";

@Component({
  selector: "app-edit-splash",
  templateUrl: "./edit-splash.component.html",
  styleUrls: ["./edit-splash.component.css", "./edit-splash.scss"]
})
export class EditSplashComponent implements OnInit {
  private episodeIdSubscription: Subscription;
  private episodeSubscription: Subscription;

  private episodeId: string;
  private episode: Episode = new Episode();
  private episodeTest: Episode;
  private episodes: Episode[];
  private updatedEpisodes: Episode[] = [];

  constructor(
    private editService: EditEpisodeService,
    private dataService: InMemoryDataService
  ) {}

  ngOnInit() {
    this.episodeIdSubscription = this.editService
      .checkEpisodeId()
      .subscribe(a => {
        this.episodeId = a;
        console.log(this.episodeId);
      });
    this.getEditingEpisode();
    this.episode.Description = this.episodeTest.Description;
    this.episode.Title = this.episodeTest.Title;
  }

  closeSplash() {
    this.editService.doneEditing();
  }

  getEditingEpisode() {
    this.episodeSubscription = this.dataService.getEpisode().subscribe(a => {
      this.episodes = a;
      console.log("adding episode info");
      console.log(this.episodes);
      this.episodeTest = this.episodes.find(
        e => e.Episode_ID.toString() === this.episodeId
      );
    });
  }

  updateEpisode() {
    this.episodeTest.Description = this.episode.Description;
    this.episodeTest.Title = this.episode.Title;
    this.closeSplash();
  }
}
